* {
  box-sizing: border-box;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.3rem;
}

body {
  font-family: "OpenSansReg";
  background-color: rgb(240, 240, 240);
}

abbr[title] {
  border: none;
  text-decoration: none;
  cursor: pointer;
}

.app {
  position: relative;
}

input[type="submit"] {
  background-color: rgba(255, 145, 0, 0.9);
  border-radius: 5px;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
}
